import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import parse from "html-react-parser";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import Header from "../ComponentChilds/Header";
import Footer from "../ComponentChilds/Footer";

export default function BlogDetailPage() {
  const { blogName } = useParams();
  // const decodedTitle = decodeURIComponent(blogName).replace(/-/g, " ");
  const [blog, setBlog] = useState(null);
  const [allBlogs, setAllBlogs] = useState([]);
  const [relatedBlogs, setRelatedBlogs] = useState([]);
  const [loading, setLoading] = useState(true);
  // const cleanUrl = blog?.featured_image?.replace(/^\/Media\/Media/, "/Media");

  useEffect(() => {
    setLoading(true);

    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/blog/"
        : "https://allmaxsport.com/api/blog/";

    async function loadBlogDetail() {
      try {
        const response = await fetch(API_URL);
        const data = await response.json();

        const titleFromURL = decodeURIComponent(blogName).replace(/-/g, " ");

        const blog = data.find((item) => item.title === titleFromURL);
        const filterRelatedBlogs = data
          .filter((blogItem) => blogItem !== blog)
          .slice(0, 3);

        setBlog(blog);
        setAllBlogs(data);
        setRelatedBlogs(filterRelatedBlogs);
      } catch (err) {
      } finally {
        setLoading(false);
      }
    }

    loadBlogDetail();
  }, [blogName]);

  if (loading) {
    return (
      <div className="max-w-5xl mx-auto px-4 py-10 space-y-6 animate-pulse">
        <div className="h-8 bg-gray-300 rounded w-3/4" />
        <div className="h-64 bg-gray-300 rounded" />
        <div className="space-y-3">
          <div className="h-4 bg-gray-300 rounded w-full" />
          <div className="h-4 bg-gray-300 rounded w-5/6" />
          <div className="h-4 bg-gray-300 rounded w-4/6" />
        </div>
      </div>
    );
  }

  return (
    <>
      <Header />
      <div className="max-w-5xl mx-auto px-4 py-10">
        {/* SEO Helmet */}
        <Helmet>
          <title>{`${blog.title} | آلمکس اسپورت`}</title>
          <meta
            name="description"
            content={blog.excerpt || blog.summary || blog.title}
          />
          <meta name="keywords" content={(blog.tags || []).join(", ")} />
          <meta name="author" content={blog.author || "آلمکس اسپورت"} />
          <meta name="robots" content="index, follow" />
          <link
            rel="canonical"
            href={`https://allmaxsport.com/blogs/${blogName}`}
          />

          {/* Open Graph Tags */}
          <meta property="og:title" content={blog.title} />
          <meta
            property="og:description"
            content={blog.excerpt || blog.summary}
          />
          <meta
            property="og:image"
            content={blog.image || blog.featured_image}
          />
          <meta property="og:type" content="article" />
          <meta
            property="og:url"
            content={`https://allmaxsport.com/blogs/${blogName}`}
          />
          <meta property="og:site_name" content="آلمکس اسپورت" />

          {/* Twitter Card Tags */}
          <meta name="twitter:card" content="summary_large_image" />
          <meta name="twitter:title" content={blog.title} />
          <meta
            name="twitter:description"
            content={blog.excerpt || blog.summary}
          />
          <meta
            name="twitter:image"
            content={blog.image || blog.featured_image}
          />

          {/* Schema.org JSON-LD */}
          <script type="application/ld+json">
            {JSON.stringify({
              "@context": "https://schema.org",
              "@type": "BlogPosting",
              mainEntityOfPage: {
                "@type": "WebPage",
                "@id": `https://allmaxsport.com/blogs/${blogName}`,
              },
              headline: blog.title,
              description: blog.excerpt || blog.summary,
              image: [blog.image || blog.featured_image],
              author: {
                "@type": "Person",
                name: blog.author || "آلمکس اسپورت",
              },
              publisher: {
                "@type": "Organization",
                name: "آلمکس اسپورت",
                logo: {
                  "@type": "ImageObject",
                  url: "https://allmaxsport.com/logo.png",
                },
              },
              datePublished: blog.created_at,
              dateModified: blog.modify_date || blog.created_at,
            })}
          </script>

          <script type="application/ld+json">
            {JSON.stringify({
              "@context": "https://schema.org",
              "@type": "Blog",
              name: "وبلاگ آلمکس اسپورت",
              url: "https://allmaxsport.com/blogs",
              description:
                "آخرین مقالات و مطالب آموزشی از آلمکس اسپورت درباره تمرین، تغذیه، برنامه ورزشی و سبک زندگی سالم.",
              publisher: {
                "@type": "Organization",
                name: "آلمکس اسپورت",
                logo: {
                  "@type": "ImageObject",
                  url: "https://allmaxsport.com/logo.png",
                },
              },
              blogPost: allBlogs.slice(0, 5).map((blog) => ({
                "@type": "BlogPosting",
                headline: blog.title,
                url: `https://allmaxsport.com/blogs/${blog.slug}`,
                image: blog.featured_image,
                author: {
                  "@type": "Person",
                  name: blog.author || "آلمکس اسپورت",
                },
                datePublished: blog.created_at,
                dateModified: blog.modify_date || blog.created_at,
                description: blog.excerpt,
              })),
            })}
          </script>
        </Helmet>

        {/* Title */}
        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 mb-4">
          {blog.title}
        </h1>

        {/* Meta */}
        <div className="text-sm text-gray-500 mb-4">
          <span>{new Date(blog?.modify_date).toLocaleDateString("fa-IR")}</span>{" "}
          • <span>{blog?.category.category}</span>• <span>{blog?.author}</span>
        </div>

        {/* Image */}
        <img
          src={blog?.featured_image}
          alt={blog?.title}
          className="w-full rounded-xl mb-6 shadow"
        />

        {/* Content */}
        <div className="prose prose-sm sm:prose lg:prose-lg max-w-none prose-img:rounded-lg prose-img:shadow">
          {parse(blog.content)}
        </div>

        {/* Tags */}
        <div className="mt-8 flex flex-wrap gap-2 text-sm text-blue-700 font-medium">
          {blog.tags.map((tag) => {
            let realName = tag.name;

            try {
              realName = JSON.parse(tag.name.replace(/'/g, '"')).name;
            } catch (e) {
            }

            return (
              <span key={tag.id} className="bg-blue-50 px-3 py-1 rounded-full">
                # {realName}
              </span>
            );
          })}
        </div>

        {/* Related Blogs */}
        <div className="mt-16">
          <h2 className="text-2xl font-bold text-gray-800 mb-6">مطالب مرتبط</h2>
          <div className="grid sm:grid-cols-2 gap-6">
            {relatedBlogs.map((item) => (
              <Link
                key={item.id}
                to={`/blogs/${encodeURIComponent(
                  item.title.replace(/\s+/g, "-")
                )}`}
                className="block group bg-white border rounded-xl overflow-hidden shadow hover:shadow-lg transition"
              >
                <img
                  // src={item.featured_image.replace(/^\/Media\/Media/, "/Media")}
                  src={`http://127.0.0.1:8000${item.featured_image.replace(
                    /^\/Media\/Media/,
                    "/Media"
                  )}`}
                  alt={item.title}
                  className="w-full h-40 object-cover group-hover:scale-105 transition-transform duration-300"
                />
                <div className="p-4">
                  <h3 className="text-lg font-semibold text-gray-800 group-hover:text-blue-600">
                    {item.title}
                  </h3>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}
